import React from "react"
import { Link } from "gatsby"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

const FishTank = () => {
  const data = useStaticQuery(graphql`
    query {
      chevron: file(relativePath: { eq: "icons/chevron_site.png" }) {
        childImageSharp {
          fluid(maxWidth: 200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Layout>
      <div style={{background: '#F4F1DE'}}>
        <SEO title="FishTank" />
        <h1 id="top" style={HeadingStyle}>FishTank</h1>
        <div style={container}>
          <div style={p}>
            <p>
            Page Coming Soon!
            </p>
          </div>
        </div>
        <Link to="#top">
            <Img style={chevronStyle} fluid={data.chevron.childImageSharp.fluid} />
        </Link>
      </div>
    </Layout>
  )
}

const HeadingStyle = {
  color: '#21A59B',
  textAlign: 'center',
  padding: '50px',
  fontFamily: 'Bitterbrush',
  fontSize: '4rem'
}


const container = {
  display: 'flex',
  justifyContent: 'space-between',
  maxWidth: `960px`,
  marginLeft: 'auto',
  marginRight: 'auto'
}

const imageStyle = {
  width: "50%"
}

const p = {
  color: '#3D405B',
  fontFamily: 'Nunito',
  width: "40%",
  textAlign: 'center',
  margin: 'auto'
}

const chevronStyle = {
  margin: 'auto',
  maxWidth: '5rem',
  maxHeight: '5rem'
}

export default FishTank
